import React, {useState} from 'react';
import './App.css';

import db from './db.json'

function App() {
    let startQuotes = db.quotes[Math.floor(Math.random() * db.quotes.length)];
    const [quotes, setQuotes] = useState(startQuotes)

    const switchQuotes = () => {
        setQuotes(db.quotes[Math.floor(Math.random() * db.quotes.length)])
    }

    return (
        <div className="App">
            <blockquote> {quotes}</blockquote>
            <button onClick={switchQuotes}>New quote</button>
        </div>
    );
}

export default App;
